import React, { Component } from 'react';

class changingPhoneNum extends Component {
    render() {
        return (
            <div className="box_change mt30">
                <dl>
                    <dt> </dt>
                    <dd>
                        <div className="input_box01">
                            <div className="ib_box01">
                                <input id="changePhone" name="change_phone" type="text" placeholder=" - ." />
                                <button className="btn_medium_g"></button>
                            </div>
                            <div className="ip_box01">
                                <input id="changeCertificationNum" name="change_certification_num" type="text" placeholder=" 6" />
                            </div>
                        </div>
                    </dd>
                </dl>
                <div className="box_btn db_align">
                    <button className="btn_small_w w70" onClick={this.props.closing} name="isChangingPhoneNum" ></button>
                    <button className="btn_small w70" onClick={this.props.closing} name="isChangingPhoneNum" ></button>
                </div>
            </div>
        );
    }
}

export default changingPhoneNum;