import React from 'react';

let date = new Date(); 
let year = date.getFullYear(); 
let month = new String(date.getMonth()+1); 
let day = new String(date.getDate()); 

//   0 . 
if(month.length == 1){ 
  month = "0" + month; 
} 
if(day.length == 1){ 
  day = "0" + day; 
} 

const UnderHeader = () => {

        return (
          <div className="breadscrumb">
            <div className="innerwrap">
              <div className="left">
                <ul>
                  <li></li>
                  <li></li>
                </ul>
              </div>
              <div className="right">
                <p>{year}.{month}.{day} </p>
              </div>
            </div>
          </div>
        );

}
export default UnderHeader;