import React, { Component } from 'react';

class changingPassword extends Component {
    render() {
        return (
            <div className="box_change">
            <dl>
                <dt> </dt>
                <dd>
                    <div className="input_box01">
                        <div className="ib_box01">
                             <input id="currentPassword" name="current_password" type="text" placeholder=" " />
                             <button className="btn_medium_g"></button>
                        </div>
                    </div> 
                </dd>
            </dl>
            <dl>
                <dt> </dt>
                <dd>
                    <div className="input_box01">
                        <div className="ip_box01">
                            <input id="changePassword01" name="change_password01" type="text" placeholder="8-16 , ,   " />
                        </div>
                         <div className="ip_box01">
                             <input id="changePassword01_01" name="change_password01_01" type="text" placeholder=" " />
                         </div>
                    </div>
                </dd>
            </dl>
            <div className="box_btn db_align">
                <button className="btn_small_w w70" onClick={this.props.closing} name="isChangingPassword" ></button>
                <button className="btn_small w70" onClick={this.props.closing} name="isChangingPassword"></button>
            </div>
        </div>
        );
    }
}

export default changingPassword;