import React, { Component } from 'react';

class changingEmail extends Component {
    render() {
        return (
            <div className="box_change mt30">
                <dl>
                    <dt> </dt>
                    <dd>
                        <div className="input_box01">
                            <div className="email_box">
                                <input type="text" name="change_email" id="changeEmail_id" placeholder="." />
                                <span>@</span>
                                <div className="select_box01">
                                    <select className="select01">
                                        <option defaultValue></option>
                                        <option>naver.com</option>
                                        <option>gmail.com</option>
                                        <option>nate.com</option>
                                    </select>
                                </div>
                            </div>
                            <div className="ib_box01">
                                <input id="changeCertificationNum02" name="change_certification_num02" type="text" placeholder=" 6" />
                                <button className="btn_medium_g"></button>
                            </div>
                        </div>
                    </dd>
                </dl>
                <div className="box_btn db_align">
                    <button className="btn_small_w w70" onClick={this.props.closing} name="isChangingEmail"></button>
                    <button className="btn_small w70" onClick={this.props.closing} name="isChangingEmail"></button>
                </div>
            </div>
        );
    }
}

export default changingEmail;