import React, { Component } from 'react';

class Company extends Component {
    render() {
        return (
            <div id="ltab01" className="login_tab1">
                <div className="input_box01">
                    <div className="select_box01">
                        <select className="select01">
                        <option defaultValue></option>
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        </select>
                    </div>
                    <div className="ip_box01"><input onChange= {this.props.change} id="find_name01" name="name" type="text" placeholder=""/></div>
                    <div className="ip_box01"><input onChange= {this.props.change} id="find_copNum01" name="companyId" type="text" placeholder=""/></div>
                    <div className="ib_box01">
                        <input onChange= {this.props.change} id="find_phoneNum01" name="phone" type="text" placeholder=" - ."/>
                        <button className="btn_medium_g" onClick={this.props.request}></button>
                    </div>
                    <div className="ip_box01">
                        <input onChange= {this.props.change} id="find_certificationNum01" name="authNum" type="text" placeholder=" 6"/>
                        <button className="btn_medium_g" onClick={this.props.verify}></button>
                    </div>
                </div>
            </div>
        );
    }
}

export default Company;