import React, { Component } from 'react';
import Modal from 'react-responsive-modal';
import classNames from 'classnames/bind';
import axios from 'axios';

const axios1 = axios.create({
    withCredentials: true
})


const nopad = {
    padding: '0px',
}

class Main extends Component {
    setGrade = (e) => {
        this.setState({
            grade: e.target.text
        })
    }
    componentDidMount() {
        this.getUser();
    }
    getUser = () => {
        this.setState({
            fetching: true
        });
        const { email } = this.props.location.state;
        const arr = email.split('@');
        axios1.get(`https://dev.hchecker.org/users/${arr[0]}%40${arr[1]}`)
            .then(res => {
                if (res.data.success) {
                    const user=res.data.user;
                    this.setState({
                        name : user.name,
                        id: user.companyId,
                        company: user.company_name,
                        phone: user.phone
                    })
                }
            })
            .catch(e => { console.log(e); });
        this.setState({
            fetching: false
        })     
    }

    state= {
        option:false
    }
    
    onCloseModal = () => {
        console.log("make state false");
        this.setState({ open: false });
        this.props.history.goBack();
    };
    render() {
        return (
            <Modal open={true} onClose={this.onCloseModal} center style={nopad}>
                <div className="popup pop01 grade_pop01">
                    <div className="pop_header">
                        
                <button className="btn_close" type="button" onClick={this.onCloseModal}></button>
                    </div>
                    <div className="pop_content">
                        <div className="pop_top">
                            <p className="tit"> ?</p>
                            <p className="txt">     .</p>
                            <div className="box_grade">
                                <div className="requester_info">
                                    <p> : <span className="requester_name">{this.state.name} </span>[ : <span className="requester_num">{this.state.id}</span>]</p>
                                    <p> : <span className="requester_belong bar">{this.state.company}</span>  : <span className="requester_department">{this.state.phone}</span></p>
                                </div>
                                
                                <div onClick={() => this.setState({ option: !this.state.option })} className={classNames("selectBox select_box08", { 'open': this.state.option })}>
                                    <div className="select_btn">
                                        <p><span></span></p>
                                    </div>
                                    <div className="option_list">
                                        <ul>
                                            <li><a onClick={this.setGrade}>G1</a></li>
                                            <li><a onClick={this.setGrade}>G2</a></li>
                                            <li><a onClick={this.setGrade}>G3</a></li>
                                            <li><a onClick={this.setGrade}>G4</a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="pop_bottom">
                            <button className="btn_big" onClick={this.handleAccept}></button>
                        </div>
                    </div>
                </div>
            </Modal>
        );
    }
};

export default Main;

