import React, { Component } from 'react';
import LogoChecker from 'asset/images/logo_checker.png';
import axios from 'axios';

const axios1 = axios.create({
    withCredentials: true
  })

class Main extends Component {
    state={redirect:false};

    redirect = () => {
        if (this.state.redirect) {
            this.props.history.push('/');
        }
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name] : e.target.value,
        })
    }

    // :      put /users/email
    handleSubmit = (e) => {
        e.preventDefault();
        if (this.state.password != this.state.passwordVerify) {
            alert("  .");
        } 
        console.log(":",this.props.location.state.email,"\n:",this.props.location.state.name, "\n:",this.props.location.state.phone,"\n:",this.state.password);
        axios1.put(`https://dev.hchecker.org/users/${this.props.location.state.email}`,  {
            email: this.props.location.state.email,
            name: this.props.location.state.name,
            password: this.state.password,
            phone: this.props.location.state.phone

        }).then(res => {
            if (res.data.success) {
                console.log("  ");
            }
            this.setState({
                redirect: true
            });
        }
        ).catch(e => {
            console.log("  ");
        })
    }
    render() {
        return (
            <article id="contentsWrap" className="login login02 login04">
            {this.redirect()}
                <div className="box_contents">
                    <div className="box_logo">
                        <img src={LogoChecker}/>
                    </div>
                    <form action="#" onSubmit={this.handleSubmit} acceptCharset="utf-8" name="login06" method="get">
                        <div className="box_login">
                            <div className="box_top">
                                <p>  .</p>
                            </div>
                            <div className="box_mid">
                                <div className="input_box01">
                                    <div className="ip_box01"><input onChange={this.handleChange} id="find_password01" name="password" type="text" placeholder="8-16 , ,   " /></div>
                                    <div className="ip_box01"><input onChange={this.handleChange} id="find_password01_1" name="passwordVerify" type="text" placeholder=" " /></div>
                                </div>
                            </div>
                            <div className="box_bottom">
                                <button className="btn_big"></button>
                            </div>
                        </div>
                    </form>
                </div>
            </article>
        );
    }
}

export default Main;