import React from 'react';
import {BrowserRouter} from 'react-router-dom';
import App from 'pgs/Details';
import 'asset/common/css/web_style.css';

const Root = () => {
    return (
        <BrowserRouter
        basename="/seasall-front">
            <App/>
        </BrowserRouter>
    );
};

export default Root;
