import React, { Component } from 'react';
import ChangingPassword from './changingPassword';
import ChangingPhoneNum from './changingPhoneNum';
import ChangingEmail from './changingEmail';
import Navi from 'common/Navi';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state={
            isChangingPassword: false,
            isChangingPhoneNum: false,
            isChangingEmail: false,
        }
        this.handleOpening = this.handleOpening.bind(this);
        this.handleClosing = this.handleClosing.bind(this);
    }
    handleOpening = (e) => {
        this.setState({
            [e.target.name]: true,
        });
    }
    handleClosing = (e) => {
        this.setState({
            [e.target.name]: false,
        });
    }
    render() {
        return (
            <div>
                <article id="contentsWrap" className="mypage01">
                    <div className="innerwrap">
                        <Navi />
                        <div className="contents_area">
                            <div className="panel">
                                <div className="panel_header">
                                    <div className="left">
                                        <h2></h2>
                                    </div>
                                    <div className="right"></div>
                                </div>
                                <div className="panel_contents">
                                    <div className="panel02">
                                        <div className="panel_header"><span></span></div>
                                        <div className="panel_contents">
                                            <table className="table01">
                                                <colgroup>
                                                    <col width="15.5%" />
                                                    <col width="84.5%" />
                                                </colgroup>
                                                <tbody>
                                                    <tr>
                                                        <th></th>
                                                        <td><p>[G1]</p></td>
                                                    </tr>
                                                    <tr>
                                                        <th>/</th>
                                                        <td><p>/</p></td>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <td>
                                                            {this.state.isChangingPassword == false
                                                            ?
                                                            <button onClick={this.handleOpening} name="isChangingPassword" className="btn_small w70"></button>
                                                            :
                                                            <ChangingPassword closing={this.handleClosing} /> }
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <td>
                                                            <p>+82 10-1234-5678</p>
                                                            {this.state.isChangingPhoneNum == false
                                                            ?
                                                            <div className="box_btn">
                                                                <button onClick={this.handleOpening} name="isChangingPhoneNum" className="btn_small w70"></button>
                                                            </div>
                                                            :
                                                            <ChangingPhoneNum closing={this.handleClosing} /> }
                                                            
                                                        </td>
                                                    </tr>
                                                    <tr>
                                                        <th></th>
                                                        <td>
                                                            <p>lhsworker@hyundai.com</p>

                                                            {this.state.isChangingEmail == false
                                                            ?
                                                            <div className="box_btn">
                                                                <button onClick={this.handleOpening} name="isChangingEmail" className="btn_small w70"></button>
                                                            </div>
                                                            :
                                                            <ChangingEmail closing={this.handleClosing} />
                                                        }

                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </article>
            </div>
        );
    }
}

export default Main;