import React, {Component} from 'react';
import Logo from 'images/icon.svg';
import Modal from 'react-responsive-modal';

const nopad = {
    padding: '0px',
}

class Main extends Component {
    onCloseImgModal = () => {
        console.log("make state false");
        this.setState({ imgopen: false });
        //window.location.replace("http://localhost:3000/print_home")
        this.props.history.goBack();
    };
    render(){
        return (

            <Modal open='true' onClose={this.onCloseImgModal} center style={nopad}>       
                <div className="modal-innerbox">
                    <div className="modal-header"> </div>
                    <img src={Logo} className="ask-logo" alt=""/>
                    <div className="modal-content">   ?
                    <hr id="askhr"></hr>
                     01000000
                     aa@aa.com
                    </div>
                </div>
            </Modal>
        );
    }
};

export default Main;