import React, { Component } from 'react';
import Navi from 'common/Navi';
import Tab1 from './Tab1';
import Tab2 from './Tab2';

class Main extends Component {
	state={
		tab:""
	}
	handleClick=(e)=> {
		this.setState({
			tab: e.target.text,
		})
	}
    render() {
        return (
            <article id="contentsWrap" className="grade01">
			<div className="innerwrap">
				<form action="#" acceptCharset="utf-8" name="grade01" method="get">
                <Navi />
				<div className="contents_area">
					<div className="tab02">
			            <ul>
			                {/*  tab02      - on_tab */}
			                <li onClick={this.handleClick}><a className={this.state.tab==""?"on_tab":null}></a></li>
			                <li onClick={this.handleClick}><a className={this.state.tab==""?"on_tab":null}></a></li>
			            </ul>
			        </div>
			        <div className="tab02_cont">
			        	<div className="gtab02">
						{this.state.tab==""? <Tab1 /> : <Tab2 />}

						</div>
					</div>
				</div>
			</form>
		</div>
	</article>
        );
    }
}

export default Main;