import React, { Component } from 'react';
import axios from 'axios';
import { AccordionTitle } from 'semantic-ui-react';

const axios1 = axios.create({
    withCredentials: true
})

class Item extends Component {


    constructor(props) {
        super(props);
        this.state = {
            clicked: 0,
            type: "all",
            registered: false
        }
    }



    handleType = (e) => {
        this.setState({
            type: e.target.value,
        })
    }
    handleClick = () => {
        const { title, content, type, file, clicked } = this.state;
        const { id, userId } = this.props;
        this.setState({
            clicked: clicked + 1
        })
        if (this.state.clicked > 1) {
            this.props.postReply(title, content, type, file, id, userId);
            this.setState({
                registered : true
            })
        }

    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            clicked: 0
        })
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        });
    }

    getReply = () => {
        //console.log("id = "+this.props.id);
        axios1.get(`https://dev.hchecker.org/replies/${this.props.id}`)
            .then(res => {
                if (res.data.success) {
                    const reply = res.data.reply;
                    this.setState({
                        reply
                    })
                    //console.log("reply id = "+reply.id);
                }
            })
            .catch(e => { console.log(e); });

    }

    render() {

        const { call, id, createdAt, type, parts, state } = this.props;
        //console.log(this.state.reply);
        return (
            <div className={`tr ${this.state.clicked > 0 ? "open" : null}`}>
                <div className="group_fixed">
                    <div className="td">
                        <p className="report_sts complete">
                            <span className="icon"></span>
                            <span className="txt">{state == 1 ? "" : ""}</span>
                        </p>
                    </div>
                    <div className="td"><p>{id}</p></div>
                    <div className="td"><p>{createdAt.slice(0, 10)}</p></div>
                    <div className="td"><p>{type}</p></div>
                    <div className="td"><p className="serial_code">{parts}<br />CONTACT ASSY-CLOCK</p></div>
                </div>

                {this.state.registere
                    ?
                    <div className="box_answer answer_after">
                        <div className="answer_top">
                            <div className="title">   .   .</div>
                            <div className="title_info">
                                <dl>
                                    <dt> : </dt>
                                    <dd></dd>
                                </dl>
                                <dl>
                                    <dt> : </dt>
                                    <dd></dd>
                                </dl>
                            </div>
                        </div>
                        <div className="answer_bottom">
                            <div className="answer_con">
                                <p>        . 1588-7278  .          . 1588-7278  .</p>
                                <p>        . 1588-7278  .          . 1588-7278  .</p>
                            </div>
                            <div className="cf_file">
                                <dl>
                                    <dt></dt>
                                    <dd>
                                        <a href="#">20181212 .jpg</a>
                                    </dd>
                                </dl>
                            </div>
                        </div>
                    </div>
                    :
                    <div className="box_answer answer_before">
                        <div className="answer_top">
                            <div className="ip_box02">
                                <input onChange={this.handleChange} id="reportTitle" name="title" type="text" placeholder="" />
                            </div>
                            <div className="select_box03">
                                <select value={this.state.type} className="select03" onChange={this.handleType}>
                                    <option value="all" defaultValue></option>
                                    <option value="11">111</option>
                                    <option value="22">222</option>
                                    <option value="33">333</option>
                                </select>
                            </div>
                        </div>
                        <div className="answer_bottom">
                            <textarea onChange={this.handleChange} placeholder="" name="content"></textarea>
                            {/* 
                        <div className="box_cffile">
                        <dl>
                            <dt></dt>
                            <dd>
                            <div className="drop_box">
                                        <a href="#" className="btn_file">   ,    .</a>
                                        <input type="file" id="fileadd" name="fileName" required />
                                        <label for="fileadd"> </label>
                            </div>
                            </dd>
                        </dl>
                        </div>
                        */}
                        </div>
                    </div>
                }

                <div className="group_variable">
                    {/* 
            {this.state.reply ?
            <p>2018.12.31 PM 3:00</p>
            :
            <button className="btn_small w70" onClick={this.handleClick}></button>
            }*/}

                    <p className="btn_small w70" onClick={this.handleClick}></p>

                </div>
            </div>
        );
    }
};

const styles = {
    dis: {
        color: "pink",

    }
}
export default Item;
